* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

body {
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
}

img,
svg {
  max-width: 100%;
}

a,
a:hover,
a:visited,
a:focus {
  text-decoration: none;
}

.module {
  width: 1440px;
  display: grid;
  grid-template-columns: 1fr repeat(2, 2fr) 1fr;
  gap: 5%;
  padding-top: 96px;
}

.module__content {
  grid-column: 3/4;
  justify-self: left;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.module__image {
  grid-column: 2/3;
  justify-self: right;
  height: 300px;
}

.module__button {
  width: 128px;
  height: 48px;
  padding: 16px 40px 16px 40px;
  background-color: #e4c1ae;
  border-radius: 32px;
  font-family: Roboto;
  font-size: 1rem;
  color: #fff;
  text-align: center;
}

.module__title {
  font-family: Roboto;
  font-style: normal;
  font-size: 2.625rem;
  color: #121212;
}

.module__text {
  font-family: Roboto;
  font-style: normal;
  font-size: 1rem;
  color: #121212;
  text-align: justify;
  line-height: 150%;
}